.user-data {
   list-style: none;
    display:flex;
}
.user-container{
    width: 70%;
    margin-left: 20px;
    margin-right: 10px;
    border-right: 1px solid lightgray;
}
.user-container ul{
    list-style: none;
}
.user-container ul li{
    display:flex;
    border-bottom: 1px solid #ccc;
    padding-bottom: 10px;
    margin-bottom: 10px;
}
.user-container ul li img{
    margin-right: 10px;
}